<template>
  <div class="wrapper" :class="{ error }">
    <input
      type="text"
      :value="value"
      :disabled="disabled"
      :readonly="readonly"
      @change="$emit('change',$event.target.value)"
      @input="$emit('input',$event.target.value)"
      @focus="$emit('focus',$event.target.value)"
      @blur="$emit('blur',$event.target.value)"
    />
    <template v-if="error">
      <icon name="error" class="icon-error"></icon>
      <span class="error-message">{{ error }}</span>
    </template>
  </div>
</template>
<script>
import Icon from "../icon/icon";
export default {
  components: { Icon },
  name: "GuluInput",
  props: {
    value: {
      type: String,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    error: {
      type: String,
    },
  },
};
</script>
<style lang="less" scoped>
@height: 32px;
@border-color: #999;
@red: #F1453D;
@border-color-hover: #666;
@font-size: 12px;
@box-shadow-color:rgba(0,0,0,0.5);
.wrapper {
  font-size: @font-size;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  vertical-align: middle;
  > :not(:last-child){margin-right: .5rem;}
  > input {
    height: 32px;
    border: 1px solid @border-color;
    border-radius: 4px;
    padding: 0 8px;
    font-size: inherit;
    &:hover {
      border-color: @border-color-hover;
    }
    &:focus {
      box-shadow: inset 0 1px 3px @box-shadow-color;
      outline: none;
    }
    &[disabled] {
      border-color: #bbb;
      color: #bbb;
      cursor: not-allowed;
    }
    &[readonly] {
      border-color: #bbb;
      color: #bbb;
    }
  }
  &.error {
    > input {
      border-color: @red;
    }
  }
  .icon-error{
    fill: @red;
  }
  .error-message{
    color: @red;
  }
}
</style>